//  countSubtotal           cardsInCart  cardsList.
// cardsInCart -  ',        .
// cardsList -  ',          .

export const countSubtotal = (cardsInCart, cardsList) => {
  //   subtotalArr,         .
  //       '  cardsInCart      cardsList  id.
  //             .
  const subtotalArr = cardsInCart.map(({ id, count }) => {
    const currentCard = cardsList.find(({ id }) => id)
    return currentCard && count * currentCard.price //  null,    
  })

  //       ,    subtotalArr.
  //    (sum)   0.
  const subTotal = subtotalArr.reduce((sum, value) => {
    return sum + value
  }, 0)

  return subTotal //      .
}
