//       
export const getCardsList = async () => {
  //        
  const response = await fetch('./cardListMy.json')

  // ,     ( 200 OK)
  if (!response.ok) {
    throw new Error(' - ' + response.status) //    200 OK,     
  }

  //     JSON
  return response.json()
}

