import React from 'react' //   React
import Card from '../Card/Card' //   Card
// import PropTypes from 'prop-types'; //  PropTypes    
import styles from './CardsList.module.scss' //    

const CardsList = ({
  cards,
  onClickHandler,
  changeFavouriteHandler,
  favouritesCardsArr,
}) => {
  //   Card   '   cards
  const cardsComponents = cards.map(({ title, price, id, color, imgSrc }) => {
    return (
      <Card
        key={id}
        title={title}
        price={price}
        id={id}
        color={color}
        imgSrc={imgSrc}
        isFavourite={favouritesCardsArr.includes(id)}
        onClickHandler={onClickHandler}
        changeFavouriteHandler={changeFavouriteHandler}
      />
    )
  })

  return (
    //    Card    
    <ul className={styles.list}>{cardsComponents}</ul>
  )
}

// CardsList.propTypes = {
//     cards: PropTypes.array.isRequired, //  "cards"     '
//     onClickHandler: PropTypes.func.isRequired, //  "onClickHandler"     '
//     changeFavouriteHandler: PropTypes.func.isRequired, //  "changeFavouriteHandler"     '
//     favouritesCardsArr: PropTypes.array //  "favouritesCardsArr"  '     
// };

CardsList.defaultProps = {
  cards: [], //  ,   "cards"  ,    
  favouritesCardsArr: [], //  ,   "favouritesCardsArr"  ,     
}

export default CardsList //   CardsList
