import { ErrorMessage, useField } from 'formik' //   ErrorMessage  useField   Formik
import styles from '../CartForm.module.scss' //    
import { NumericFormat } from 'react-number-format' //   NumberFormat     
// import PropTypes from 'prop-types'; //  PropTypes    

export const NumberFormatInputBlock = ({
  name,
  label,
  id,
  placeholder,
  type,
}) => {
  const [field] = useField(name) //  useField    

  return (
    <div key={id} className={styles.fieldContainer}>
      <label className={styles.orderLabel} htmlFor={name}>
        {label}
      </label>
      <NumericFormat
        {...field} //     ,   useField,  NumberFormat
        className={styles.orderInput}
        format="" //   
        mask="_" //       "_"
        name={name}
        placeholder={placeholder}
        type={type}
      />
      <ErrorMessage component="p" className={styles.fieldError} name={name} />
    </div>
  )
}

