//     id: PropTypes.string,
//     name: PropTypes.string.isRequired, // name  '  
//     placeholder: PropTypes.string, // placeholder  '  
//     type: PropTypes.string, // type  '  
//     label: PropTypes.string.isRequired, // label  '  
// }

NumberFormatInputBlock.defaultProps = {
  placeholder: '', //  ,  placeholder  ,    
  type: 'text', //  ,  type  ,   'text'
  id: '', //  ,  id  ,    
}
