//   CartList -      cards   onClickHandler,      .         CardInCart        . propTypes  defaultProps            .

// import PropTypes from 'prop-types'; //  PropTypes     
import styles from './CartList.module.scss' //    
import CardInCart from '../CardInCart/CardInCart' //   `CardInCart`,       

const CartList = ({ cards, onClickHandler }) => {
  let cardsComponents = cards.map(({ title, price, id, color, imgSrc }) => {
    return (
      <CardInCart
        key={id}
        title={title}
        price={price}
        id={id}
        color={color}
        imgSrc={imgSrc}
        onClickHandler={onClickHandler}
      />
    )
  })

  return <ul className={styles.list}>{cardsComponents}</ul>
}

// CartList.propTypes = {
//     cards: PropTypes.array, //  `cards`    
//     onClickHandler: PropTypes.func.isRequired, //  `onClickHandler`      '
// }

CartList.defaultProps = {
  cards: [], //  ,  `cards`  ,    
}

export default CartList
