import React, { useEffect } from 'react'
import { fetchCardsList } from '../../store/cards/actions' //         .
import styles from './Cart.module.scss'
import CartList from '../../components/CartList/CartList' //        .
import { useDispatch, useSelector } from 'react-redux' //       Redux.
import { removeFromCart } from '../../store/cart/actions' //       .
import Loader from '../../components/Loader/Loader' //   .
import { CartForm } from '../../components/CartForm/CartForm' //      .
import OrderTotals from '../../components/OrderTotals/OrderTotals' //       .

const Cart = () => {
  const isLoading = useSelector(({ cards }) => cards.isLoading) //         Redux.
  const cardsList = useSelector(({ cards }) => cards.cards) //       Redux.
  const cardsInCart = useSelector(({ cardsInCart }) => cardsInCart) //        Redux.
  const hasError = useSelector(({ hasError }) => hasError) //      Redux.
  const dispatch = useDispatch() //   dispatch      Redux.

  useEffect(() => {
    dispatch(fetchCardsList()) //          .
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const deleteFromCartHandler = (id) => {
    dispatch(removeFromCart(id)) //         Redux.
  }

  let content

  if (hasError) {
    content = <div>Sorry, error</div> //    ,   .
  } else {
    const filteredCards = cardsList.filter(({ id }) => {
      return cardsInCart.find(({ id }) => {
        return id
      })
    })
    content = (
      <>
        <CartList
          cards={filteredCards}
          onClickHandler={deleteFromCartHandler}
        />
        {/*     . */}
        <div className={styles.totalBlock}>
          <OrderTotals />
          {/* //     . */}
        </div>
        <CartForm />
        {/* //   . */}
      </>
    )
  }

  return (
    <div className={styles.cartSection}>
      <div className={styles.container}>
        <h2 className={styles.cartTitle}>1. Products - {cardsInCart.length}</h2>{' '}
        {/* //       . */}
        {isLoading ? <Loader /> : ''}
        {/* //              . */}
        {cardsInCart.length >= 1 ? (
          <div className={styles.cartInner}>
            <ul className={styles.listTitles}>
              <li>Photo</li>
              <li>Description</li>
              <li>Price</li>
              <li>Quantity</li>
              <li>Total</li>
            </ul>
            {content}
          </div>
        ) : (
          (content = <p className={styles.noItemsTitle}>No items in cart</p>)
        )}{' '}
        {/* //                 . */}
      </div>
    </div>
  )
}

export default Cart
