import React from 'react'
// import PropTypes from 'prop-types'; //   PropTypes    
import styles from './CardInCart.module.scss' //    
import TrashIcon from './TrashIcon' //    
import { useDispatch, useSelector } from 'react-redux' //   useDispatch  useSelector  Redux
import {
  increaseProductQuantity,
  decreaseProductQuantity,
} from '../../store/cart/actions' //         
import CheckoutList from '../CheckoutList/CheckoutList.module.scss' //     

const NOIMGSRC = 'img/notfound.png' //   ,  ,  imgSrc 

const CardInCart = ({
  id, //   ()
  imgSrc, // URL  
  title, //  
  color, //  
  price, //   ()
  isPriceShow, //   ( )
  onClickHandler, //     
  hasQuantityBtns, //       ( )
  hasTrashIcon, //    ( )
}) => {
  const cardsInCart = useSelector(({ cardsInCart }) => cardsInCart) //    Redux   useSelector
  const currentCard = cardsInCart.find(({ id }) => id === id) //        
  const dispatch = useDispatch() //   dispatch  Redux   

  const decrementHandler = () => {
    dispatch(decreaseProductQuantity(id)) //        
  }

  const incrementHandler = () => {
    dispatch(increaseProductQuantity(id)) //        
  }

  return (
    <li className={styles.item}>
      <img
        src={imgSrc ? imgSrc : NOIMGSRC}
        alt={title}
        className={styles.img}
      />
      <div className={styles.itemInfo}>
        <h3 className={styles.itemTitle}>{title}</h3>
        <p className={styles.color}>Color: {color}</p>
        <p className={styles.id}>id: {id}</p>
      </div>
      {isPriceShow ? <p className={styles.price}>{price} UAH</p> : ''}
      <p className={styles.quantity}>
        {hasQuantityBtns ? (
          <button
            className={`${styles.quantityBtn} ${styles.decrement}`}
            onClick={decrementHandler}
          >
            -
          </button>
        ) : (
          ''
        )}
        <span>{currentCard.count}</span>
        {hasQuantityBtns ? (
          <button
            className={`${styles.quantityBtn} ${styles.increment}`}
            onClick={incrementHandler}
          >
            +
          </button>
        ) : (
          ''
        )}
      </p>
      <p className={styles.total}>{currentCard.count * price} UAH</p>
      {hasTrashIcon ? (
        <button
          className={styles.deleteItemBtn}
          onClick={() => {
            onClickHandler(id)
          }}
        >
          <TrashIcon />
        </button>
      ) : (
        ''
      )}
    </li>
  )
}

